import React from 'react';
import { Layout, Space } from 'antd';
import Logo from '../assets/logo/angkringan247-logo.png'

const { Header, Footer, Content } = Layout;
const headerStyle = {
    color: '#2bb67d',
};
const contentStyle = {
    color: '#2bb67d',
};
const footerStyle = {
    color: '#2bb67d',
};

export default function Bang() {
    return (
        <Space
            direction="vertical"
            style={{
            width: '100%',
            }}
            size={[0, 48]}
        >
            <Layout className='min-h-screen bg-[#010101]'>
                <Header style={headerStyle} className='mt-10 bg-[#010101]'>
                    <div>
                        <nav>
                            <ul className='flex justify-center items-center text-sm tracking-widest gap-20 text-[#2bb67d]'>
                                <li className='hidden md:block'>HOME</li>
                                <li className='hidden md:block'>HOME</li>
                                <img className="h-28 w-auto" src={Logo} alt='Angkringan247 Logo'></img>
                                <li className='hidden md:block'>HOME</li>
                                <li className='hidden md:block'>HOME</li>
                            </ul>
                        </nav>
                    </div>
                </Header>
                <Content className="my-10 flex justify-center items-center bg-[#010101]" style={contentStyle}>
                    <div className='flex justify-center text-white'>
                        <div className='font-montserrat my-auto text-center'>
                            <p className='text-[#94a1b1] text-md md:text-2xl tracking-widest'>DISCORD COMMUNITY</p>
                            <p className='font-stretchpro text-2xl md:text-7xl text-[#ffffff] py-3'>ANGKRINGAN247</p>
                            <p className='tracking-widest text-md text-[#94a1b1] md:text-3xl'><span className='mr-2 text-[#2bb67d]'>10.000</span>TOTAL MEMBERS</p>
                            <div className='mt-16'>
                                <a href='https://discord.gg/angkringan247' className='px-10 py-4 bg-[#2bb67d] text-[#010101] font-bold tracking-widest text-sm md:text-xl'>LETS JOIN ON DISCORD</a>
                            </div>
                        </div>
                    </div>
                </Content>
                <Footer style={footerStyle} className='mb-10 bg-[#010101]'>
                    <div className='text-white flex justify-center items-center'>
                        <div className='font-montserrat flex flex-col md:flex-row md:gap-20'>
                            <a href='https://google.com'>
                                <div className='flex px-2'>
                                    <i className='text-4xl fa-brands fa-discord p-5'></i>
                                    <div className='m-auto'>
                                        <p className='text-[#2bb67d] font-semibold tracking-widest text-[10px]'>DISCORD</p>
                                        <p className='font-bold text-lg'>ANGKRINGAN247</p>
                                    </div>
                                </div>
                            </a>
                            <a href='https://google.com'>
                                <div className='flex px-2'>
                                    <i className='text-4xl fa-brands fa-instagram p-5'></i>
                                    <div className='m-auto'>
                                        <p className='text-[#2bb67d] font-semibold tracking-widest text-[10px]'>INSTRAGRAM</p>
                                        <p className='font-bold text-lg'>ANGKRINGAN247</p>
                                    </div>
                                </div>
                            </a>
                            <a href='https://google.com'>
                                <div className='flex px-2'>
                                    <i className='text-4xl fa-brands fa-youtube p-5'></i>
                                    <div className='m-auto'>
                                        <p className='text-[#2bb67d] font-semibold tracking-widest text-[10px]'>YOUTUBE</p>
                                        <p className='font-bold text-lg'>ANGKRINGAN247</p>
                                    </div>
                                </div>
                            </a>
                        </div>
                    </div>
                </Footer>
            </Layout>
        </Space>
    )
};